*{
  margin: 0;
}
body{
  background-color: #f5f6fa;
}

.app_header{
  color: #fc3c3c;
  font-size: 2rem;
}

.App{
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
}
.app_header{
  display: flex;
  align-items: center;
  margin: 20px;
  justify-content: space-between;
}
.app_dropdown{
  background-color: white;
}
.app_left{
  flex: 0.9;
}
.app_right{
  display: flex;
  flex-direction: column;
}
.app_right .MuiCardContent{
  display: flex;
  flex-direction: column;
  flex-grow: 1; 
}

.app_stats{
  display: flex;
  justify-content: space-between;
}
.app_graph{
  flex-grow: 1;
}

.app_graph_title{
  margin-top: 10px;
}
@media (max-width:990px) {
  .App{
    flex-direction: column;
  }
}