body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
}
body{
  background-color: #f5f6fa;
}

.app_header{
  color: #fc3c3c;
  font-size: 2rem;
}

.App{
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
}
.app_header{
  display: flex;
  align-items: center;
  margin: 20px;
  justify-content: space-between;
}
.app_dropdown{
  background-color: white;
}
.app_left{
  flex: 0.9 1;
}
.app_right{
  display: flex;
  flex-direction: column;
}
.app_right .MuiCardContent{
  display: flex;
  flex-direction: column;
  flex-grow: 1; 
}

.app_stats{
  display: flex;
  justify-content: space-between;
}
.app_graph{
  flex-grow: 1;
}

.app_graph_title{
  margin-top: 10px;
}
@media (max-width:990px) {
  .App{
    flex-direction: column;
  }
}
.infoBox{
    flex:1 1;
    cursor: pointer;
}
.infoBox_cases{
    color: #cc1034;
    font-weight: 600;
    font-size: 1.75rem;
    margin-bottom: 0.5rem;
}
.infoBox:not(:last-child){
    margin-right: 15px;
}
.infoBox--selected{
    border-top: 10px solid greenyellow;
}
.infoBox--red{
    border-color: red;
}
.infoBox__cases--green{
    color: lightgreen !important;
}
.infoBox_total{
    color: #6c757d;
    font-weight: 700 !important;
    font-size: 0.8 rem !important;
    margin-top: 15px !important;
}
.map{
    height: 500px;
    background-color: white;
    padding: 1rem;
    border-radius: 20px;
    margin-top: 16px;
    box-shadow: 0 0 8px -4px rgba(0,0,0,0.5);
}

.info_container{
    width: 150px;
}
.info_flag img{
    width:100px;
    border-radius: 5px;
}

.info_flag{
    height: 80px;
    width: 100%;
    background-size: cover;
    border-radius: 8px;
}

.info_confirmed,
.info_recovered,
.info_deaths{
    font-size: 16px;
    margin-top: 5px;
}

.info_name{
    font-size: 20px;
    font-weight: bold;
    color: #555;
}
.map .leaflet-container{
    height: 100%;
}
.table{
    margin-top: 20px;
    overflow: scroll;
    color: #6a5d5d;
    height: 400px;
    background-color: lightgrey;
}
.table tr{
    display: flex;
    justify-content: space-between;
}

.table td{
    padding: 0.5rem;
    border: none;
}
.table tr:nth-of-type(odd){
    background-color: #f3f2f8;
}
